import React from "react";
import { Link } from "react-router-dom";
import Button from "../button/Button.component";
import "./hero-background.styles.scss";

export default function HeroBackground({
  src,
  type = "video",
  videoTitle,
  miniTitle,
}) {
  console.log("bg in ", src, type);
  return (
    <div className="hero-img-bg">
      {type.toLowerCase() === "image" ? (
        <div
          className="bg-container"
          style={{ backgroundImage: "url(" + src + ")" }}
        ></div>
      ) : (
        <div className="videobg-container">
          <video autoPlay="autoplay" loop="loop" muted className="bgVideo">
            <source src={src} type="video/mp4" />
            Your browser does not support the video tag.
          </video>
          <div className="content">
            <h3 className="mini-heading">{miniTitle}</h3>
            <h1 className="heading">{videoTitle}</h1>
            <Link to="/pages/sippy-cups">
              <Button>
                <span>Shop Now</span>
              </Button>
            </Link>
          </div>
        </div>
      )}
    </div>
  );
}
